import Link from 'next/link'
import React from 'react'

const Navigation = () => {
  return (
    <nav className="px-4 py-4 lg:absolute lg:top-4 lg:right-12 lg:z-20">
        <div className="flex justify-between items-center lg:block">
            <div className="text-xl font-medium lg:hidden">Gregory Odiase</div>
            <div className="space-x-4 lg:space-x-8">
            <Link href="/about" className="hover:text-aqua">About</Link>
            <Link href="/projects" className="hover:text-aqua">Projects</Link>
            <Link href="/contacts" className="hover:text-aqua">Contacts</Link>
            </div>
        </div>
    </nav>
  )
}

export default Navigation

